<template>
  <v-container
    fluid
    grid-list-md>
    <v-form
      ref="form2"
      @submit.prevent="validateBeforeSubmit">
      <core-progress-modal :show="loading" />
      <v-layout
        v-if="!loading"
        row
        wrap>
        <v-flex md12>
          <v-card class="elevation-0">
            <v-card-title>
              <h3 class="headline mb-0">Selecione os lançamentos</h3>
            </v-card-title>
            <v-card-text>
              <v-layout
                row
                wrap>
                <v-flex md3>
                  <core-label-value
                    :label="'Rede'"
                    :value="value.redeNome"
                  />
                </v-flex>
                <v-flex md3>
                  <core-label-value
                    :label="'Empresa'"
                    :value="value.empresaNome"
                  />
                </v-flex>
                <v-flex md6>
                  <core-label-value
                    :label="'Loja'"
                    :value="value.lojaNome"
                  />
                </v-flex>
                <v-flex md2>
                  <core-label-value
                    :label="'Vencimento até'"
                    :value="value.vencimentoAte | moment('DD/MM/YYYY')"
                  />
                </v-flex>
                <v-flex md2>
                  <core-label-value
                    :label="'Pagamento em'"
                    :value="value.pagamentoEm | moment('DD/MM/YYYY')"
                  />
                </v-flex>
                <v-flex md3>
                  <core-label-value
                    :label="'Comissão compras (% a.m.)'"
                    :value="value.peComissaoCompra"
                  />
                </v-flex>
                <v-flex md3>
                  <core-label-value
                    :label="'Desconto antecipação (% a.m.)'"
                    :value="value.peDescontoAntecipacao"
                  />
                </v-flex>
              </v-layout>
            </v-card-text>
          </v-card>
        </v-flex>
        <v-flex md12>
          <v-card class="elevation-0">
            <v-card-text>
              <v-layout
                row
                wrap>
                <v-flex md1>
                  <core-label-value
                    :label="'Compras'"
                    :value="vlTotalSelecionado('vlTotalCompras')"
                  />
                </v-flex>
                <v-flex md2>
                  <core-label-value
                    :label="'Comissão compras'"
                    :value="vlTotalSelecionado('vlTotalComissaoCompras')"
                  />
                </v-flex>
                <v-flex md1>
                  <core-label-value
                    :label="'Antecipação compras'"
                    :value="vlTotalSelecionado('vlTotalAntecipacaoCompras')"
                  />
                </v-flex>
                <v-flex md2>
                  <core-label-value
                    :label="'Créditos admin'"
                    :value="vlTotalSelecionado('vlTotalCreditoAdmin')"
                  />
                </v-flex>
                <v-flex md2>
                  <core-label-value
                    :label="'Pagamentos loja'"
                    :value="vlTotalSelecionado('vlTotalPagamentosLoja')"
                  />
                </v-flex>
                <v-flex md2>
                  <core-label-value
                    :label="'Créditos lojista'"
                    :value="vlTotalSelecionado('vlTotalCreditoLojista')"
                  />
                </v-flex>
                <v-flex md2>
                  <core-label-value
                    :label="'Valor a pagar'"
                    :value="vlTotalSelecionado('vlTotalPagar')"
                    :class-value="'headline font-weight-black'"
                  />
                </v-flex>
              </v-layout>
            </v-card-text>
          </v-card>
        </v-flex>
        <v-flex md12>
          <v-card>
            <v-data-table
              v-model="selected"
              :headers="headers"
              :items="items"
              class="elevation-1 arrow-pagintion"
              hide-actions
              select-all
              item-key="id"
            >
              <template
                slot="headers"
                scope="props"
              >
                <tr>
                  <th>
                    <v-checkbox
                      :input-value="props.all"
                      :indeterminate="props.indeterminate"
                      primary
                      hide-details
                      @click.native="toggleAll"
                    />
                  </th>
                  <th
                    v-for="header in props.headers"
                    :key="header.text"
                    :class="header.class"
                    :align="header.align"
                  >
                    {{ header.text }}
                  </th>
                </tr>
              </template>
              <template
                slot="items"
                scope="props"
                ma-5>
                <tr
                  :active="props.selected"
                  @click="props.selected = !props.selected">
                  <td>
                    <v-checkbox
                      :input-value="props.selected"
                      primary
                      hide-details
                    />
                  </td>
                  <td class="text-xs-center">{{ props.item.vencimento | moment("DD/MM/YYYY") }}</td>
                  <td class="text-xs-right">{{ props.item.vlTotalCompras.toLocaleString("pt-BR", { minimumFractionDigits: 2 }) }}</td>
                  <td
                    v-negative="props.item.vlTotalComissaoCompras"
                    class="text-xs-right">
                    {{ props.item.vlTotalComissaoCompras.toLocaleString("pt-BR", { minimumFractionDigits: 2 }) }}
                  </td>
                  <td
                    v-negative="props.item.vlTotalAntecipacaoCompras"
                    class="text-xs-right">
                    {{ props.item.vlTotalAntecipacaoCompras.toLocaleString("pt-BR", { minimumFractionDigits: 2 }) }}
                  </td>
                  <td
                    v-negative="props.item.vlTotalCreditoAdmin"
                    class="text-xs-right">
                    {{ props.item.vlTotalCreditoAdmin.toLocaleString("pt-BR", { minimumFractionDigits: 2 }) }}
                  </td>
                  <td
                    v-negative="props.item.vlTotalPagamentosLoja"
                    class="text-xs-right">
                    {{ props.item.vlTotalPagamentosLoja.toLocaleString("pt-BR", { minimumFractionDigits: 2 }) }}
                  </td>
                  <td class="text-xs-right">{{ props.item.vlTotalCreditoLojista.toLocaleString("pt-BR", { minimumFractionDigits: 2 }) }}</td>
                  <td
                    v-negative="props.item.vlTotalPagar"
                    class="text-xs-right">
                    {{ props.item.vlTotalPagar.toLocaleString("pt-BR", { minimumFractionDigits: 2 }) }}
                  </td>
                </tr>
              </template>
            </v-data-table>
          </v-card>
        </v-flex>
        <v-flex md12>
          <v-layout class="justify-end">
            <v-btn
              dark
              @click="goBack">Voltar</v-btn>

            <v-btn
              :loading="loadingBtn"
              type="submit"
              color="primary">Continuar</v-btn>
          </v-layout>
        </v-flex>
      </v-layout>
    </v-form>
  </v-container>
</template>

<script>
import { MONEY } from '../../../constants'
import { RepasseBusiness } from '../../../business'
export default {
  props: {
    value: {
      type: Object,
      required: true
    },
    // eslint-disable-next-line vue/require-default-prop
    setData: {
      type: Function,
      requided: true
    },
    // eslint-disable-next-line vue/require-default-prop
    goBack: {
      type: Function,
      requided: true
    },
    // eslint-disable-next-line vue/require-default-prop
    nextStep: {
      type: Function
    }
  },
  data () {
    return {
      selected: [],
      items: [],
      money: MONEY,
      loadingBtn: false,
      headers: [
        {
          sortable: false,
          text: 'Vencimento',
          value: 'dataVencimento',
          class: 'grey lighten-1'
        },
        {
          sortable: false,
          text: 'Compras',
          value: 'compras',
          class: 'grey lighten-1',
          align: 'right'
        },
        {
          sortable: false,
          text: 'Comissão compras',
          value: 'comissaoCompras',
          class: 'grey lighten-1',
          align: 'right'
        },
        {
          sortable: false,
          text: 'Antecipação compras',
          value: 'antecipacaoCompras',
          class: 'grey lighten-1',
          align: 'right'
        },
        {
          sortable: false,
          text: 'Créditos admin',
          value: 'creditosAdmin',
          class: 'grey lighten-1',
          align: 'right'
        },
        {
          sortable: false,
          text: 'Pagtos em loja',
          value: 'pagtosLoja',
          class: 'grey lighten-1',
          align: 'right'
        },
        {
          sortable: false,
          text: 'Créditos lojista',
          value: 'creditosLojista',
          class: 'grey lighten-1',
          align: 'right'
        },
        {
          sortable: false,
          text: 'Valor a pagar',
          value: 'valorPagar',
          class: 'grey lighten-1',
          align: 'right'
        }
      ],
      totalItems: null,
      loading: false
    }
  },
  watch: {
    value () {
      this.$emit('input', this.value)
    }
  },
  beforeMount () {
    this.loading = true
    RepasseBusiness.calculaRepasseIndividual(this.value)
      .then(response => {
        this.items = response.data
      })
      .finally(() => {
        this.loading = false
        this.toggleAll()
      })
  },
  methods: {
    toggleAll () {
      if (this.selected.length) this.selected = []
      else this.selected = this.items.slice()
    },
    validateBeforeSubmit () {
      this.$validator.validate().then(result => {
        if (result) {
          this.loadingBtn = true
          this.value.itemsSelecionados = this.selected.map(value => value.vencimento)
          this.nextStep()
        } else {
          this.$root.showAlerta(this.$vuetify.t('$vuetify.Erro.camposObrigatorios'))
        }
      })
    },
    vlTotalSelecionado (attr) {
      if (this.selected.length === 0) {
        this.value.extratoRepasse[attr] = '0,00'
      } else {
        this.value.extratoRepasse[attr] = this.selected
          .map(item => item[attr])
          .reduce((total, valor) => total + valor)
          .toLocaleString('pt-BR', { minimumFractionDigits: 2 })
      }
      return this.value.extratoRepasse[attr]
    }
  }
}
</script>
